.g-coop {
  background: #fff;
  padding: 10px 20px;
  height: 100%;
}
.g-coop .m-coop-menu {
  margin: 10px 0;
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
}
.g-coop .col-des {
  width: 90%;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}
.g-coop .m-coop-detail .item {
  display: flex;
  flex-direction: row;
  height: 1000px;
}
.g-coop .m-coop-detail .title {
  color: #ff2a00;
}
