.g-sche {
  background: #fff;
  padding: 10px 20px;
  height: 100%;
  position: relative;
}
.g-sche .m-loader {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(255, 255, 255, 0.8);
  z-index: 100;
  display: flex;
  justify-content: center;
  align-items: center;
}
.g-sche .m-appy {
  background-color: white;
  padding: 10px;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.05), -1px -1px 1px rgba(0, 0, 0, 0.05);
  border: 1px solid #eee;
}
.g-sche .m-appy-tl {
  font-size: 20px;
  font-weight: 600;
  color: #3366aa;
  margin-bottom: 10px;
  text-align: center;
}
.g-sche .m-date {
  font-size: 12px;
}
.g-sche .m-upload {
  position: relative;
  display: inline-block;
  margin-left: 5px;
  width: 110px;
  height: 32px;
}
.g-sche .m-upload-btn {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 999999;
}
.g-sche .m-file-item {
  width: 100px;
  height: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid #999;
  border-radius: 5px;
  padding: 5px;
  font-size: 12px;
  margin: 5px;
  position: relative;
}
.g-sche .m-file-item > i {
  width: 24px;
  height: 24px;
  margin: 10px 0;
}
.g-sche .m-file-item svg {
  height: 100%;
  width: 100%;
}
.g-sche .m-files {
  display: flex;
}
.g-sche .m-file-item p {
  flex: 1 1;
  margin: 0;
  display: flex;
  align-items: center;
}
.g-sche .m-file-item:hover {
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.1), -2px 2px 2px rgba(0, 0, 0, 0.1);
  color: #0066cc;
}
.g-sche .m-file-item:hover .m-del {
  display: block;
}
.g-sche .m-fun-wrap {
  display: flex;
}
.g-sche .m-fun-wrap > button,
.g-sche .m-fun-wrap > span {
  margin-left: 5px;
}
